import React, { useState, useEffect } from "react";
import { FaBars } from "react-icons/fa";

import logonova from "../../assets/logonova-completa.svg"
import Menu from "./Menu"

import "./Header.scss"

function Header() {
  const [menuOpen, setMenuOpen] = useState(false);

  useEffect(() => {
    const closeMenuIfLargeScreen = () => { // para fechar o menu se ele estiver aberto e a tela  aumentada
      if (window.innerWidth > 970 && menuOpen) {
        setMenuOpen(false);
      }
    };
  
    window.addEventListener("resize", closeMenuIfLargeScreen);
  
    return () => {
      window.removeEventListener("resize", closeMenuIfLargeScreen);
    };
  }, [menuOpen]);

  useEffect(() => {
    if (menuOpen) {
      const closeMenuOnOutsideClick = (e) => { // para fechar o menu se o usurio clica em algum lugar aleatrio (fora do menu)
        if (!e.target.closest(".button-group") && !e.target.closest(".menu-icon")) {
          // verificando se o clique no ocorreu dentro do menu ou no cone do menu
          setMenuOpen(false);
        }
      };

      document.addEventListener("click", closeMenuOnOutsideClick);

      return () => {
        document.removeEventListener("click", closeMenuOnOutsideClick);
      };
    }
  }, [menuOpen]);

  return (
    <div className="header">
      <img src={logonova} className="logo" alt="logo" />
      <div className="menu-icon" onClick={() => setMenuOpen(!menuOpen)}>
        <FaBars />
      </div>
      <Menu menuOpen={menuOpen}/>
    </div>
  )
}

export default Header
