import React from 'react';
import photo01 from "../../assets/event-photo-01-recepcao-CPII.jpg"
import photo02 from "../../assets/event-photo-02-recepcao-CPII.jpg"
import photo03 from "../../assets/arduino-tangua01.jpeg"
import photo04 from "../../assets/arduino-tangua02.jpeg"
import photo05 from "../../assets/arduino-tangua03.jpeg"
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay } from 'swiper/modules';
import 'swiper/css';
import './Gallery.scss'


const Carousel1 = () => {

    const item = [
        <div>
            <img className='item' src={photo01} alt="Recepo alunos CP II 01" />
        </div>,
        <div>
            <img className='item' src={photo02} alt="Recepo alunos CP II 02" />
        </div>,
        <div>
            <img className='item' src={photo03} alt="Arduno em Tangu 01" />
        </div>,
        <div>
            <img className='item' src={photo04} alt="Arduno em Tangu 02" />
        </div>,
        <div>
            <img className='item' src={photo05} alt="Arduno em Tangu 03" />
    
        </div>,
        <div>
            <img className='item' src={photo01} alt="Recepo alunos CP II 01" />
        </div>,
        <div>
            <img className='item' src={photo02} alt="Recepo alunos CP II 02" />
        </div>,
    ]
    return (
        <Swiper
            spaceBetween={30}
            slidesPerView={5}
            breakpoints={{
                0: {
                    slidesPerView: 1,
                },
                500: {
                    slidesPerView: 2,
                },
                750: {
                    slidesPerView: 3,
                },
                1100: {
                    slidesPerView: 4,
                },
                1350: {
                    slidesPerView: 5,
                },
                1500: {
                    slidesPerView: 6,
                }
            }}
            onSlideChange={() => console.log('slide change')}
            onSwiper={(swiper) => console.log(swiper)}
            dir='rtl'
            autoplay={{
                delay: 1500,
                disableOnInteraction: false

            }}
            modules={[Autoplay]}
            style={{ padding: 30 }}
        >
            <div>
                {item.map((item, index) => (
                    <SwiperSlide className='slider' key={index}>{item}</SwiperSlide>

                ))}
            </div>

        </Swiper>
    );
};

export default Carousel1;