import React from "react";
import Button from "@material-ui/core/Button";
import { withStyles } from "@material-ui/core/styles";
import "./Text.scss";

const ColorButton = withStyles(() => ({
  root: {
    textTransform: "none",
    borderRadius: 55,
    fontFamily: "Lato",
    fontWeight: 300,
    backgroundColor: "#096262",
    "&:hover": {
      backgroundColor: "#13BADE",
      fontWeight: "bold",
    },
  },
}))(Button)


class Text extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      openChat: false
    }
  }

  openChat = () => {
    this.setState({
      openChat: true
    })
  }

  render(){
    const {click} = this.props

    return (
      <div className="text-who">
        <div className="title">
          Quem somos?
        </div>
        <div className="description">
          <p>Criada em 2018, a Minerv@s Digitais  um projeto de extenso da UFRJ, associado ao projeto nacional Meninas Digitais. Temos como objetivo fomentar o interesse das meninas nas reas de Computao e das reas de Tecnologias da Informao e Comunicao (TIC) e ajudar na construo da autoconfiana das alunas da UFRJ dos cursos de Bacharelado em Cincia de Computao (BCC) e de Cincias da Matemtica e da Terra (BCMT), assim como de cursos associados, Engenharia de Computao e Informao e Engenharia de Automao.</p>
        </div>
        <div className="button-box">
          <ColorButton variant="contained" color="primary" className='button' disableElevation onClick={click}>
            Ver membros
          </ColorButton>
        </div>
      </div>
    )
  }
}

export default Text;
