import React from "react"
import barras from "./../../assets/barras.svg"
import pizza from "./../../assets/Pizza.svg"

import "./Text.scss"

class Text extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      openChat: false
    }
  }

  openChat = () => {
    this.setState({
      openChat: true
    })
  }

  render(){
    return (
      <div className="motivation-text">
        <div className="title">
          Motivaes
        </div>
        <div className="first-motivation">
          <img src={barras} alt="Grfico de barras" className="img-first-motivation"></img>
          <div className="first-motivation-text">
          Diante das alarmantes disparidades de gnero em setores como engenharia, cincia da computao e afins, nasce o projeto "Minerv@s Digitais". Esse projeto visa fortalecer a presena feminina no mbito tecnolgico por meio de educao, mentoria e eventos inclusivos. Ao enfrentar a falta de representatividade, buscamos capacitar mulheres para romper barreiras, enriquecer a inovao e contribuir para um campo mais diversificado e igualitrio, desafiando esteretipos prejudiciais e impulsionando a mudana.
          </div>
        </div>
        <div className="second-motivation">
          <div className="second-motivation-text">
            Acentuada desigualdade de gnero  evidenciada no grfico ao lado, no qual 87.7% dos alunos de computao so homens e apenas 12.3% so mulheres. Esta disparidade pode ser atribuda a fatores como esteretipos enraizados, falta de modelos femininos e ambientes no inclusivos. Para solucionar esse problema, propomos a implementao de polticas de incentivo desde a educao bsica, visando desmistificar a tecnologia e inspirar interesse nas meninas. Alm disso, iniciativas como hackathons, palestras e grupos de apoio podem criar ambientes mais acolhedores, empoderando as mulheres a seguirem carreiras na computao e contribuindo para uma representao equilibrada no campo.
          </div>
          <img src={pizza} alt="Grfico de pizza" className="img-second-motivation"></img>
        </div>
      </div>
    )
  }
}

export default Text
